import {REG} from "lib/commons/registry";

/**
 * Stylage racine d'un éditeur.
 * Permet de définir les styles transversaux à tous les binds.
 * A placer sur l'elt racine d'un du bind document et des binds racines des éditeurs en panels.
 * Les <wed:children/> de ce bind root doivent être placés dans un <box-host> pour bénéficier de stylage transversal.
 * Rappel : ces styles ne s'appliquent qu'aux balises racines des binds (pas celles qui entrent dans leurs shadowTree).
 */
REG.reg.registerSkin('box/root', 1, /* language=CSS */ `
	:host {
		display: flex;
		min-height: 0;
		min-width: 0;
		flex-direction: column;
	}

	.v {
		display: flex;
		min-height: 0;
		min-width: 0;
		flex-direction: column;
	}

	.v > box-ptritem {
		align-self: stretch;
		margin-left: initial;
		margin-right: initial;
	}

	.h {
		display: flex;
		min-height: 0;
		min-width: 0;
		flex-direction: row;
		align-items: center;
	}

	[edit-mode='h'] {
		display: none;
	}

	.v.virtual[edit-mode^='r'],
	.h.virtual[edit-mode^='r'] {
		display: none;
	}

	[selected]:not([selectin]) {
		background-color: var(--edit-select-bgcolor);
	}

	::selection {
		color: currentColor;
		background-color: var(--edit-seltext-bgcolor);
	}


	[annot=error] {
		background-color: var(--edit-forbidden-bgcolor);
	}

	[annot-diff=diffadd] {
		padding-inline-end: 10px;
		background-color: var(--edit-diffadd-bgcolor);
		--edit-bgcolor: none;
	}

	[annot-diff=diffrem] {
		padding-inline-end: 10px;
		background-color: var(--edit-diffrem-bgcolor);
		--edit-bgcolor: none;
	}

	[annot-diff=diff] {
		margin-top: 2px;
		border-inline-end: 1px solid var(--edit-diff-color);
	}

	.forbidden {
		border: 1px dashed #8c0000;
		background-color: var(--edit-forbidden-bgcolor, rgba(239, 0, 0, 0.1));
	}

	/* obligatoire pour les box sans shadowRoot. */
	box-ctn:focus,
	box-ctn-input:focus {
		outline: var(--edit-box-focus);
	}

	.commentXml {
		padding-inline-start: 1em;
	}
`)
/*::inactive-selection {*/
